import React from 'react'
import { Header } from '@/components/header'
import { Languages, Moon } from 'lucide-react'
import { BellRing, Shield, HelpCircle, Info } from 'lucide-react'
import { Switch } from '@/components/ui/switch'

export default function SettingsPage() {
  return (
    <div>
      <Header />
    <div>
      <div className='grid grid-cols-2 gap-2'>
        <button className='bg-primary text-primary-foreground hover:bg-primary/90 p-2 rounded-md h-24 flex items-center justify-center flex-col'>Language <Languages/></button>
        <button className='bg-primary text-primary-foreground hover:bg-primary/90 p-2 rounded-md h-24 flex items-center justify-center flex-col'>Theme <Switch checked={true} onCheckedChange={() => {}} /></button>
        <button className='bg-primary text-primary-foreground hover:bg-primary/90 p-2 rounded-md h-24 flex items-center justify-center flex-col'>Notifications <BellRing /></button>
        <button className='bg-primary text-primary-foreground hover:bg-primary/90 p-2 rounded-md h-24 flex items-center justify-center flex-col'>Privacy <Shield /></button>
        <button className='bg-primary text-primary-foreground hover:bg-primary/90 p-2 rounded-md h-24 flex items-center justify-center flex-col'>Help <HelpCircle /></button>
        <button className='bg-primary text-primary-foreground hover:bg-primary/90 p-2 rounded-md h-24 flex items-center justify-center flex-col'>About <Info /></button>
      </div>
      </div>    
    </div>
  )
}